









export default {
	data(){
		return {
			time:3,
			height:0,
			mySet:''
		}
	},
	mounted:function(){
		this.$nextTick(function(){
			this.go_index();
			this.getbodyHeight();
		})
	},
	methods:{
		getbodyHeight(){
			let _this = this;
			uni.getSystemInfo({
				success:function(res){
					_this.height = res.screenHeight;
				}
			})
		},
		go_index_new(){
			uni.reLaunch({ //关闭页面，跳转到指定页面 
				url:'../component/component'
			});
			clearTimeout(this.mySet); 
		},
		go_index(){
			let _this = this;
			setInterval(function(){
				_this.time -= 1; 
			},1000);
			_this.mySet = setTimeout(function(){
				/* 这是使用navigateTo不能成功跳转的原因：
				 navigateTo和redirectTo 只能打开非tabBarD的页面*/
				uni.reLaunch({
					url:'../component/component'
				})
			},3000)
		}
	}
}



